import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCart } from 'app/contexts/CartContext';
import { Button } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const CartProducts = (props) => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const { cart, removeItemFromCart } = useCart();

  return (
    <>
      {cart && (
        <div className='order_summary os-left  marg-btm30'>
          <h2>PRODUCTS</h2>
          {cart.cart_items && (
            cart.cart_items.map((row, index) => (
              <div key={'cart-summary-' + index} className="row" style={{ paddingTop: "13px" }}>
                <div className='col-sm-12 col-lg-3  marg-btm30'>
                  <a className="hover_effect1" title={row.product_name} href={`/product/details/${row.id}`}>
                    <img className='os-list-img'
                      src={row.product_image}
                      alt={row.product_name}
                    />
                  </a>
                </div>
                <div className='col-sm-12 col-lg-9'>
                  <h5>{row.product_name}</h5>
                  <p>Sold By Seller: {row.seller_name}</p>
                  <p>Unit Packing: {row.unit_packing}</p>
                  <p>Rate: {row.min_qty_net_rate} / {row.min_qty} items</p>
                  <p>
                    <span className='summary-list-price'>{process.env.REACT_APP_CURRENCY}. {row.item_total} </span>
                    <span className='summary-list-price-discard'> {process.env.REACT_APP_CURRENCY} {row.item_mrp_total}</span>
                  </p>
                </div>
                <div className='col-sm-12 col-lg-10'>
                  {/* <h6>Delivery between February 23- February 26</h6> */}
                </div>
                <div className='col-sm-12 col-lg-2'>
                  <Button onClick={() => removeItemFromCart(row.id)} variant="outlined" color="inherit" sx={{ background: "#FFF" }}>
                    Remove <DeleteIcon color="error" fontSize="small" />
                  </Button>
                </div>
              </div>
            ))
          )}
        </div>
      )}
    </>
  );
}

export default CartProducts;
