import React, { useReducer, createContext, useEffect, useContext, ReactNode, useState } from "react";
import axios from 'axios';
import swal from 'sweetalert';
import { useNavigate } from 'react-router-dom';
import useAuth from 'app/hooks/useAuth';

const CartStateContext = createContext({
  getItemQuantity: 0,
  increaseCartQuantity: '',
  decreaseCartQuantity: '',
  removeFromCart: '',

});
// let { isAuthenticated, user } = useAuth();
// 
export function useCart() {
  return useContext(CartStateContext);
}

const CartProvider = ({ children }) => {
  let { isAuthenticated, user } = useAuth();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [isMiniCartOpen, setIsMiniCartOpen] = useState(false);
  const [cartItems, setCartItems] = useState([]);
  const [cart, setCart] = useState(false);
  const [order, setOrder] = useState(false);

  const openMiniCart = () => setIsMiniCartOpen(true);
  const closeMiniCart = () => setIsMiniCartOpen(false);

  useEffect(() => {

    /**
     * Check User logged IN.
     * @author Pavan Ji <dropmail2pavan@gmail.com>
     */
    if (isAuthenticated) {
      fetchCartData();
    }

  }, [isAuthenticated]);

  /**
   * Fetch cart data.
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  const fetchCartData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/get-user-cart')
          .then(response => {
            if (response.data.response_code === 200) {
              setCartItems(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
        axios.post('/api/frontend/get-cart-summary')
          .then(response => {
            if (response.data.response_code === 200) {
              setCart(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  /**
   * Cart Items.
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */

  const totalCartItems = cartItems.length || 0;

  /**
   * Cart Quantity.
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  const cartQuantity = cartItems.reduce(
    (quantity, item) => item.quantity + quantity, 0
  );

  /**
   * Increase Cart Quantity.
   * @param {*} id  // Product ID
   * @param {*} quantity  // Product quantity
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  function increaseCartQuantity(id, quantity) {
    // setCartItems(currentItems => {
    //   if (currentItems.find(item => item.id === id) == null) {
    //     return [...currentItems, { id, quantity }];
    //   } else {
    //     return currentItems.map(item => {
    //       if (item.id === id) {
    //         return { ...item, quantity: item.quantity + quantity };
    //       } else {
    //         return item;
    //       }
    //     })
    //   }
    // })
    try {
      setLoading(true);
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/update-user-cart', {
          row_id: id,
          quantity: quantity,
        })
          .then(response => {
            if (response.data.response_code === 200) {
              fetchCartData();
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
            } else if (response.data.response_code == 504) {
            } else {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  /**
   * Decrease Cart Quantity.
   * @param {*} id  // Product ID
   * @param {*} quantity  // Product quantity
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  function decreaseCartQuantity(id, quantity) {
    setCartItems(currentItems => {
      if (currentItems.find(item => item.id === id)?.quantity === 1) {
        return currentItems.filter(item => item.id !== id);
      } else {
        return currentItems.map(item => {
          if (item.id === id) {
            return { ...item, quantity: item.quantity - quantity };
          } else {
            return item;
          }
        })
      }
    })
  }

  /**
   * Remove Product item from cart.
   * @param {*} id  // Product ID
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  const removeItemFromCart = (id) => {
    try {
      setLoading(false);
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/remove-product-from-cart', {
          row_id: id,
        })
          .then(response => {
            if (response.data.response_code === 200) {
              fetchCartData();
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
            } else if (response.data.response_code == 504) {
            } else {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  /**
   * Place order of selected products in to a Cart.
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  const orderPlaced = () => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/create-order')
          .then(response => {
            if (response.data.response_code === 200) {
              getOrder(response.data.data.order_id);
              setCartItems([]);
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
            } else {
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
            setLoading(false);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  }

  /**
   * Get Order detail from server 
   * @param {*} order_id 2103%23HEGAN-202333
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  const getOrder = (order_id) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        if (order_id) {
          axios.post('/api/frontend/get-order-details', {
            order_id: order_id
          })
            .then(response => {
              if (response.data.response_code === 200) {
                setOrder(response.data.data);
                navigate('/product/checkout/order-success/' + encodeURIComponent(order_id));
              } else {
              }
              setLoading(false);
            })
            .catch(function (error) {
              console.error(error);
              setLoading(false);
            });
        }
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  }

  /**
   * Rander Cart Provider
   * @param {*} order_id 2103%23HEGAN-202333
   * @author Pavan Ji <dropmail2pavan@gmail.com>
   */
  return (
    <CartStateContext.Provider value={{
      openMiniCart,
      closeMiniCart,
      cart,
      totalCartItems,
      cartQuantity,
      increaseCartQuantity,
      decreaseCartQuantity,
      removeItemFromCart,
      orderPlaced,
      order,
      getOrder
    }}>
      {children}
    </CartStateContext.Provider>

  );
};

export default CartProvider;