import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import { Formik } from 'formik';
import {
  Button, Grid
} from '@mui/material';
import axios from 'axios';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
});
const Address = (props) => {
  const navigate = useNavigate();
  const [profile, setProfile] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  /**
  * Fetching data from server.
  */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/profile')
          .then(response => {
            if (response.data.response_code === 200) {
              setProfile(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }
  const handleFormSubmit = (values, errors) => {
    props.next(values);
  }

  return (
    <>
      <Formik
        onSubmit={(values, errors) => {
          handleFormSubmit(values, errors);
        }
        }
        initialValues={props.formData}
        validationSchema={validationSchema}
      >
        {({ values, errors, touched, handleChange, handleBlur, handleSubmit, setFieldValue }) => (
          <form onSubmit={handleSubmit}>
            <Grid container spacing={3}>
              <Grid item lg={12} sm={12} xs={12}>
                <div className='order_summary1 os-left  marg-btm30'>
                  <h2>Deliver to:</h2>
                  <div className='row' style={{ paddingTop: "13px" }}>
                    <div className='col-sm-12 col-lg-10'>
                      <h5>{profile.first_name} {profile.last_name}</h5>
                      <p>Firm name: {profile.firm_name}</p>
                      <p>Street: {profile.address}</p>
                      <p>State/province/area: {profile.state_name}</p>
                      <p>Pin code {profile.pin_code}</p>
                      <p>{profile.mobile_no}</p>
                    </div>
                    {/* <div className='col-sm-12 col-lg-2'>
                      <Button variant="outlined" color="info">
                        Change
                      </Button>
                    </div> */}
                  </div>

                </div>
              </Grid>
            </Grid>
            <Grid container style={{ padding: '10px 20px 0px' }} spacing={4}>
              <Grid item sm={6} xs={12} lg={5}>
              </Grid>
              <Grid item sm={4} xs={12} lg={2}>
                <Button
                  fullWidth
                  variant="contained"
                  type='submit'
                  sx={{ mb: 2, mt: 1 }}
                >
                  Continue
                </Button>
              </Grid>
              <Grid item sm={6} xs={12} lg={5}>
              </Grid>
            </Grid>
          </form>
        )}
      </Formik>
    </>

  );
}

export default Address;
