import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import axios from 'axios';
import {
  Button, Alert,
  TextField,
  Table, TableBody, TableCell, TableRow,
  Grid, Select, MenuItem, FormControl,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import * as Yup from 'yup';
import { Formik } from 'formik';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import { useCart } from 'app/contexts/CartContext';

const validationSchema = Yup.object().shape({

});
const PaymentDetails = (props) => {
  const [loading, setLoading] = useState(false);
  const [background, setBackground] = useState(
    (props.currentStep === 2) ? "#FFF" : "#ECFEFF"
  );
  const navigate = useNavigate();
  const { cart } = useCart();

  const handleFormSubmit = (values, errors) => {
    if (props.currentStep === 2) {
      props.next(values, true);
    } else {
      props.next(values);
    }
  }

  return (
    <>
      {cart && (
        <Formik
          onSubmit={(values, errors) => {
            handleFormSubmit(values, errors);
          }
          }
          initialValues={props.formData}
          validationSchema={validationSchema}
        >
          {({ values, errors, touched, handleChange, handleBlur, handleSubmit, setFieldValue }) => (
            <form onSubmit={handleSubmit}>
              <div className='order_summary os-left' style={{ background: `${background}` }}>
                <h2>PAYMENT DETAILS</h2>
                <Table sx={{ border: "0px" }} aria-label="simple table">
                  <TableBody sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                    <TableRow>
                      <TableCell align="left">MRP Total</TableCell>
                      <TableCell align="right">{process.env.REACT_APP_CURRENCY}. {cart.cart_mrp_total}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell align="left">Additional Discount</TableCell>
                      <TableCell align="right">{process.env.REACT_APP_CURRENCY}. {cart.cart_additional_discount_total}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell align="left">Total Amount</TableCell>
                      <TableCell align="right">{process.env.REACT_APP_CURRENCY}. {cart.cart_total}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell align="center" colSpan={2} sx={{ wordBreak: "unset" }}>
                        <Alert severity="error" icon={false}>
                          *Delivery charges are applicable when the product is delivered based on the actual weight of the consignment.
                        </Alert>
                      </TableCell>
                    </TableRow>
                    {/* <TableRow>
                      <TableCell align="left" colSpan={2}></TableCell>
                    </TableRow>
                    <TableRow className='row-highlighter'>
                      <TableCell align="left" colSpan={2}>Add products worth Rs.20.00 more and get FREE delivery</TableCell>
                    </TableRow> */}
                    <TableRow>
                      <TableCell align="left" colSpan={2}></TableCell>
                    </TableRow>
                    {/* <TableRow>
                      <TableCell align="left">Total Payable</TableCell>
                      <TableCell align="right">{process.env.REACT_APP_CURRENCY}. {cart.total_payable}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell align="left" colSpan={2}></TableCell>
                    </TableRow> */}
                    <TableRow className='row-highlighter'>
                      <TableCell align="left">Total Savings</TableCell>
                      <TableCell align="right">{process.env.REACT_APP_CURRENCY}. {cart.total_savings}</TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell align="left" colSpan={2}></TableCell>
                    </TableRow>
                    <TableRow className='total-payable'>
                      <TableCell align="left">
                        <span className='price-text'>Total Payable</span>
                        <span className='price'>{process.env.REACT_APP_CURRENCY}. {cart.total_payable}</span>
                      </TableCell>
                      <TableCell align="right">
                        <Button type='submit' variant="contained" color="success" size='large' sx={{ fontSize: '20px', fontWeight: "700" }}>
                          {(props.currentStep === 2) ? "Proceed" : "Continue"}
                        </Button>
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              </div>
            </form>
          )}
        </Formik >
      )}
    </>
  );
}
export default PaymentDetails;
