import { useState, useEffect } from "react";
import { Link, useParams, NavLink, useNavigate } from 'react-router-dom';
import { Breadcrumb, SimpleCard } from "app/components";
import { LoadingButton } from '@mui/lab';
import { Formik, Form, Field, FieldArray } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';
import {
  Card,
  Divider,
  CardContent,
  CardHeader,
  Box,
  Grid,
  Icon,
  IconButton,
  Button,
  styled,
  TextField,
  useTheme,
  RadioGroup, FormControlLabel, Radio

} from "@mui/material";


const Item = styled("div")(({ theme }) => (
  {
    marginBottom: "5px",
    fontSize: '16px',
    fontWeight: '400',
    color: '#484747'
  }
));
const ItemContent = styled("div")(({ theme }) => (
  {
    marginBottom: "15px",
    fontSize: '16px',
    fontWeight: '600',
  }
));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

// form field validation schema
const validationSchema = Yup.object().shape({
  // status: Yup.string()
  //   .required('User Accton is required!'),
  status: Yup.string().when('status', {
    is: (status) => status === 'pending',
    then: Yup.string().required('Please checked Approved or Rejected action!'),
    otherwise: Yup.string(),
  }),
}, [['status', 'status']]);

const MyProfile = () => {

  const { user_id } = useParams();
  const theme = useTheme();
  const navigate = useNavigate();
  const [user, setUser] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/profile')
          .then(response => {
            if (response.data.response_code === 200) {
              setUser(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }


  const handleFormSubmit = (values, errors) => {

    try {
      if (values.status == 'pending') {
        swal({
          title: "Warning!",
          text: 'Please select Approved or Rejected approvel action!',
          icon: "warning",
        });
        return false;
      }
      setLoading(true);
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/user-update-status-commission', {
          user_id: values.id,
          status: values.status,
          userCategories: values.userCategories,
        })
          .then(response => {
            if (response.data.response_code === 200) {
              setUser(response.data.data);
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              // navigate('/');
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "My Profile" }]} />
        </Box>

        <Card title="My Profile">
          <ContentBox>
            <CardHeader
              title="My Profile"
              titleTypographyProps={{
                variant: "h4",
                fontSize: '18px',
                fontWeight: '600',
                // textTransform: 'uppercase',
                color: theme.palette.primary.main
              }}
              action={
                <>
                  <Button title="Edit" component={Link}
                    to={`/dashboard/profile/edit`}
                    color="info" variant="outlined" size="small" sx={{ m: .5 }}>
                    Edit
                  </Button>
                  <Button title="Back" component={Link}
                    to={`/dashboard`}
                    color="primary" variant="outlined" size="small" sx={{ m: .5 }}>
                    Back
                  </Button>
                </>
              }
            />
            <Divider />
            <CardContent>
              <Box width="100%" overflow="auto">
                {user && (
                  <>
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>First Name</Item>
                        <ItemContent>{(user.first_name) ? user.first_name : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Last Name</Item>
                        <ItemContent>{(user.last_name) ? user.last_name : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Email</Item>
                        <ItemContent>{(user.email) ? user.email : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Mobile No.</Item>
                        <ItemContent>{(user.mobile_no) ? user.mobile_no : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Firm Name</Item>
                        <ItemContent>{(user.firm_name) ? user.firm_name : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>User Code</Item>
                        <ItemContent>{(user.user_name) ? user.user_name : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Address</Item>
                        <ItemContent>{(user.address) ? user.address : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>State</Item>
                        <ItemContent>{(user.state_name) ? user.state_name : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Pin Code</Item>
                        <ItemContent>{(user.pin_code) ? user.pin_code : '-'}</ItemContent>
                      </Grid>
                    </Grid>
                    <Divider />
                    <br />
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>DL No 1</Item>
                        <ItemContent>{(user.dl_no_1) ? user.dl_no_1 : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>DL No 2</Item>
                        <ItemContent>{(user.dl_no_2 ? user.dl_no_2 : '-')}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>GST No.</Item>
                        <ItemContent>{(user.gst_no) ? user.gst_no : '-'}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>FSSAI No.</Item>
                        <ItemContent>{(user.fssai_no) ? user.fssai_no : '-'}</ItemContent>
                      </Grid>
                    </Grid>
                    <Divider />
                    <br />
                    <Grid item sm={6} xs={12} lg={3}>
                      <Item>Register As</Item>
                      <ItemContent>{(() => {
                        switch (user.register_as) {
                          case 'M':
                            return 'Manufacturer'
                          case 'D':
                            return 'Distributor'
                          case 'W':
                            return 'Wholesaler'
                          case 'R':
                            return 'Retailer'
                          case 'H':
                            return 'Hospital'
                          default:
                            return null
                        }
                      })()}
                      </ItemContent>
                    </Grid>
                    {(() => {
                      switch (user.register_as) {
                        case 'R':
                          return ''
                        case 'H':
                          return ''
                        default:
                          return (
                            <>
                              <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }} >Bank Details</h4>
                              <Grid container>
                                <Grid item sm={6} xs={12} lg={3}>
                                  <Item>Account Name</Item>
                                  <ItemContent>{user.account_name}</ItemContent>
                                </Grid>
                                <Grid item sm={6} xs={12} lg={3}>
                                  <Item>Account No.</Item>
                                  <ItemContent>{user.account_no}</ItemContent>
                                </Grid>
                                <Grid item sm={6} xs={12} lg={3}>
                                  <Item>Branch</Item>
                                  <ItemContent>{user.branch}</ItemContent>
                                </Grid>
                                <Grid item sm={6} xs={12} lg={3}>
                                  <Item>IFSC Code</Item>
                                  <ItemContent>{user.ifsc_code}</ItemContent>
                                </Grid>
                                <Grid item sm={6} xs={12} lg={3}>
                                  <Item>Signature</Item>
                                  <ItemContent>{(user.signature) ? (
                                    <>
                                      <img src={user.signature} alt="Signature" style={{ width: '100px' }} />
                                    </>
                                  ) : <>No Signature found.</>}</ItemContent>
                                </Grid>
                              </Grid>
                            </>
                          )
                      }
                    })()}

                    <Divider />
                    <br />
                    <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }}>Commissions</h4>
                    <br />

                    {user.userCategories ? user.userCategories
                      .map((row, index) => (
                        <Grid container spacing={2} key={'1_gc_' + (1 + index)}>
                          <Grid item sm={6} xs={12} lg={3} key={'1_g_' + (1 + index)}>
                            <Item key={'1_gi_' + (1 + index)}>Category Name</Item>
                            <ItemContent key={'1_gic_' + (1 + index)}>
                              {user.userCategories[index].name}
                            </ItemContent>
                          </Grid>
                          {(() => {
                            switch (user.register_as) {
                              case 'R':
                                return ''
                              case 'H':
                                return ''
                              default:
                                return (
                                  <>
                                    <Grid item sm={6} xs={12} lg={3} key={'2_g_' + (1 + index)}>
                                      <Item key={'2_gi_' + (1 + index)}>Commission %</Item>
                                      <ItemContent key={'2_gic_' + (1 + index)}>
                                        {user.userCategories[index].commission}
                                      </ItemContent>
                                    </Grid>
                                    <Grid item sm={6} xs={12} lg={3} key={'3_g_' + (1 + index)}>
                                      <Item key={'3_gi_' + (1 + index)}>Default Commission %</Item>
                                      <ItemContent key={'3_gic_' + (1 + index)}>
                                        {(() => {
                                          switch (user.register_as) {
                                            case 'M':
                                              return `${user.userCategories[index].m_commission} %`
                                            case 'D':
                                              return `${user.userCategories[index].d_commission} %`
                                            case 'W':
                                              return `${user.userCategories[index].w_commission} %`
                                            default:
                                              return null
                                          }
                                        })()}
                                      </ItemContent>
                                    </Grid>
                                  </>
                                )
                            }
                          })()}
                        </Grid>
                      )) : <></>}
                  </>
                )}
              </Box>
            </CardContent>
          </ContentBox>
        </Card>

      </Container>
    </>

  );
};

export default MyProfile;
