import React, { useEffect, useState } from 'react';

import {
  Link, NavLink, useLocation, useNavigate,
} from 'react-router-dom';
import useAuth from 'app/hooks/useAuth';
import swal from 'sweetalert';
import axios from 'axios';
import Moment from 'moment';

import OwlCarousel from 'react-owl-carousel';

import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

function HotSellersSlider() {

  const [products, setProducts] = useState(false);
  const [loading, setLoading] = useState(false);
  const location = useLocation();
  let { logout, user, isAuthenticated } = useAuth();

  useEffect(() => {
    if (isAuthenticated) {
      fetchProductData();
    } else {
      fetchData();
    }
  }, [location]);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/get-hot-seller-list')
          .then(response => {
            if (response.data.response_code === 200) {
              setProducts(response.data.data);
            } else if (response.data.response_code === 401) {
            } else {
            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  /**
   * Fetching data from server.
   */
  const fetchProductData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/get-auth-hot-seller-list')
          .then(response => {
            if (response.data.response_code === 200) {
              setProducts(response.data.data);
            } else if (response.data.response_code === 401) {
            } else {
            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }


  const options = {
    nav: true,
    dots: false,
    // autoplay: true,
    loop: true,
    margin: 20,
    responsiveClass: true,
    navText: ['<i class="ion-ios-arrow-left"></i>', '<i class="ion-ios-arrow-right"></i>'],
    smartSpeed: 1000,
    responsive: {
      0: {
        items: 2,
        dots: true,
      },
      400: {
        items: 2,
      },
      600: {
        items: 2,
      },
      700: {
        items: 3,
      },
      1000: {
        items: 4,

      }
    },
  };
  return (
    <>
      <div className="section small_pt prodstyel marg-btm30">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="heading_tab_header">
                <h3>Hot Sellers <Link to="/categories" className="vall">VIEW ALL</Link></h3>
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col-md-12">
              {products && (
                <OwlCarousel className='hot_seller product_slider carousel_slider owl-carousel owl-theme nav_style1'
                  {...options}
                >
                  {
                    products.map((row, index) => (
                      <div key={'hot-seller-pro-' + index} className="item">
                        <div className="product">
                          <div className="product_wrap">
                            <div className="sale_banner">
                              <Link to={`product/details/${row.id}`} className="hover_effect1" title={row.product_name}>
                                <img src={`${row.product_image}`} alt={row.product_name} />
                              </Link>
                              <div className='hint-pre'>
                                <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/hint.png`} alt="Discount" />
                                <span>{(row.min_qty_margin_percentage) ? row.min_qty_margin_percentage : '0'}%</span>
                              </div>
                            </div>
                            <div className="product_cont">
                              <h5>
                                <Link to={`product/details/${row.id}`} title={row.product_name}>
                                  {row.product_name}
                                </Link>
                              </h5>
                              <div className="product_price">
                                <span className="descr">Sold By Seller: {row.seller_name}</span>
                                <span className="date">Expiry Date: {(row.expiry_date) ? Moment(row.expiry_date).format('DD MMM YYYY') : 'NA'}</span>
                                <span className="pricesml">MRP ({row.mrp})</span>
                                <span className="pricebig">{process.env.REACT_APP_CURRENCY} {row.min_qty_net_rate}</span>
                                <Link to={`product/details/${row.id}`} title={row.product_name} className="pricebigcart"
                                > View Product
                                </Link>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))
                  }
                </OwlCarousel>
              )}
            </div>
          </div>
        </div>
      </div >
    </>
  );
}
export default HotSellersSlider;
