import { useState, useEffect } from "react";
import { Link, useParams, NavLink, useNavigate } from 'react-router-dom';
import { Breadcrumb, SimpleCard } from "app/components";
import { LoadingButton } from '@mui/lab';
import { Formik, Form, Field, FieldArray } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';
import {
  Card, CardContent, CardHeader,
  Divider,
  Box,
  Grid,
  Button,
  styled,
  TextField,
  useTheme,
  RadioGroup, FormControlLabel, Radio,
  FormControl, Select, MenuItem,
  FormGroup, Checkbox

} from "@mui/material";


const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));

const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

// inital login credentials
const initialValues = {
  register_as: 'M',
  plan_id: '1',
  first_name: '',
  last_name: '',
  email: '',
  firm_name: '',
  user_name: '',
  mobile_no: '',
  user_categories: [],
  address: '',
  pin_code: '',
  dl_no_1: '',
  dl_no_2: '',
  gst_no: '',
  fssai_no: '',
  account_name: '',
  account_no: '',
  ifsc_code: '',
  branch: '',
  state_id: '1',
  signature: '',
};
// form field validation schema
// .matches(/(\+91\ )[6-9]{1}[0-9 ]{4}[0-9 ]{4}[0-9]{3}/, {
const validationSchema = Yup.object().shape({
  first_name: Yup.string()
    .required('First Name is required!'),
  last_name: Yup.string()
    .required('Last Name is required!'),
  firm_name: Yup.string()
    .required('Firm Name is required!'),
  gst_no: Yup.string()
    .min(15, 'GST number must be 15 digit length!')
    .max(15, 'GST number must be 15 digit length!')
    .matches(/^([0-9]{2}[a-zA-Z]{4}([a-zA-Z]{1}|[0-9]{1})[0-9]{4}[a-zA-Z]{1}([a-zA-Z]|[0-9]){3}){0,15}$/, { message: "Please enter valid GST number.", excludeEmptyString: false }),
  mobile_no: Yup.string()
    .required('Mobile number is required!')
    .min(10, 'Mobile number must be 10 digit length!')
    .max(10, 'Mobile number must be 10 digit length!')
    .matches(/^[6-9]\d{9}$/, { message: "Please enter valid mobile number!", excludeEmptyString: false }),
  address: Yup.string()
    .required('Address is required!'),
  email: Yup.string().email('Invalid Email address').required('Email is required!'),
  address: Yup.string()
    .required('Address is required!'),
  state_id: Yup.string()
    .required('State is required!'),
  pin_code: Yup.string()
    .min(6, "Pin Code must be 6 digit!")
    .max(6, "Pin Code must be 6 digit!")
    .required('Pin Code is required!'),
  // user_categories: Yup
  //   .string()
  //   .required("Select at least one category"),
  account_name: Yup.string().when('register_as', {
    is: (register_as) => register_as == 'M' || register_as == 'D' || register_as == 'W',
    then: Yup.string().required('Account Name is required!'),
    otherwise: Yup.string(),
  }),
  account_no: Yup.string().when('register_as', {
    is: (register_as) => register_as === 'M' || register_as === 'D' || register_as === 'W',
    then: Yup.string()
      .min(10, "Account Number must be 10 to 16 digit!")
      .max(16, "Account Number must be 10 to 16 digit!")
      .required('Account Number is required!'),
    otherwise: Yup.string(),
  }),
  ifsc_code: Yup.string().when('register_as', {
    is: (register_as) => register_as === 'M' || register_as === 'D' || register_as === 'W',
    then: Yup.string()
      .min(8, "IFSC Code must be 8 to 13 alphanumeric!")
      .max(13, "IFSC Code must be 8 to 13 alphanumeric!")
      .required('IFSC Code is required!'),
    otherwise: Yup.string(),
  }),
  branch: Yup.string().when('register_as', {
    is: (register_as) => register_as === 'M' || register_as === 'D' || register_as === 'W',
    then: Yup.string()
      .min(3, "Branch must be 3 charectors!")
      .required('Branch is required!'),
    otherwise: Yup.string(),
  }),
  // signature: Yup.string().when('register_as', {
  //   is: (register_as) => register_as === 'M' || register_as === 'D' || register_as === 'W',
  //   then: Yup.string().required('Signature is required'),
  //   otherwise: Yup.string(),
  // })
});

const UpdateProfile = () => {

  const { user_id } = useParams();
  const theme = useTheme();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState(initialValues);
  const [states, setStates] = useState(false);
  const [plans, setPlans] = useState(false);
  const [categories, setCategory] = useState(false);
  const [formData, setFormData] = useState(initialValues);

  const [DLOpen, setDLOpen] = useState(false);
  const [fssaiOpen, setFssaiOpen] = useState(false);
  const [userCategories, setUserCategories] = useState([]);

  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/get-states')
          .then(response => {
            if (response.data.response_code === 200) {
              setStates(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
        axios.post('/api/client/get-medicine-categories')
          .then(response => {
            if (response.data.response_code === 200) {
              setCategory(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });

        axios.post('/api/client/get-plans')
          .then(response => {
            if (response.data.response_code === 200) {
              setPlans(response.data.data);
            } else if (response.data.response_code === 401) {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });

        axios.post('/api/client/get-client-profile-info', { user_id: user_id })
          .then(response => {
            if (response.data.response_code === 200) {
              setFormData(response.data.data);
              setUser(response.data.data);
              setUserCategories(response.data.data.user_categories);
              var selectedUserCat = response.data.data.user_categories;
              // Set Default Cat Option
              if (selectedUserCat.length == 1) {
                if (selectedUserCat[0] == 5) {
                  setDLOpen(false);
                  setFssaiOpen(false);
                } else if (selectedUserCat[0] == 4) {
                  setDLOpen(false);
                  setFssaiOpen(true);
                } else {
                  setDLOpen(true);
                  setFssaiOpen(true);
                }
              } else if (selectedUserCat.length) {
                setDLOpen(true);
                setFssaiOpen(true);
              } else {
                setDLOpen(false);
                setFssaiOpen(false);
              }

            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  const handleChange = (e) => {
    console.log(e.target.name + '-' + e.target.value);
    setFormData({ ...formData, [e.target.name]: e.target.value });
    console.log(formData);
  };

  const changeHandler = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.files[0] });
  };

  const handleFormSubmit = (values, errors) => {
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        const uploadFormData = new FormData();
        uploadFormData.append(user_id, user_id)
        for (let iKey in values) {
          if (iKey === 'update_signature' || iKey === 'user_categories') {
            uploadFormData.append(iKey, formData[iKey])
            // for (let file of formData[iKey]) {
            //   uploadFormData.append('picture', file);
            // }
          } else {
            uploadFormData.append(iKey, values[iKey])
          }

        }
        const config = { headers: { "Content-Type": "multipart/form-data" } };
        axios.post('/api/client/update-profile', uploadFormData, config)
          .then(response => {
            if (response.data.response_code === 200) {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              // navigate('/');
            } else {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Update Profile" }]} />
        </Box>

        <Card title="Update Profile">
          <ContentBox>
            <CardHeader
              title="Update Profile"
              titleTypographyProps={{
                variant: "h4",
                fontSize: '18px',
                fontWeight: '600',
                // textTransform: 'uppercase',
                color: theme.palette.primary.main
              }}
              action={
                <>
                  <Button title="Edit" component={Link}
                    to={`/dashboard/profile/edit`}
                    color="info" variant="outlined" size="small" sx={{ m: .5 }}>
                    Edit
                  </Button>
                  <Button title="Back" component={Link}
                    to={`/dashboard`}
                    color="primary" variant="outlined" size="small" sx={{ m: .5 }}>
                    Back
                  </Button>
                </>
              }
            />
            <Divider />
            <CardContent>
              <Box width="100%" overflow="auto">
                {user && (
                  <>
                    <Formik
                      onSubmit={(values, errors) => {
                        handleFormSubmit(values, errors);
                      }
                      }
                      enableReinitialize={true}
                      initialValues={user}
                      validationSchema={validationSchema}
                    >
                      {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
                        <form onSubmit={handleSubmit}>
                          <Grid container style={{ padding: '20px' }} spacing={2}>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="first_name" className='form-label'>First Name</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="first_name"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.first_name}
                                onChange={handleChange}
                                helperText={touched.first_name && errors.first_name}
                                error={Boolean(errors.first_name && touched.first_name)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="last_name" className='form-label'>Last Name</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="last_name"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.last_name}
                                onChange={handleChange}
                                helperText={touched.last_name && errors.last_name}
                                error={Boolean(errors.last_name && touched.last_name)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="email" className='form-label'>Email ID</label>
                              <TextField
                                fullWidth
                                disabled
                                size="small"
                                type="email"
                                name="email"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.email}
                                onChange={handleChange}
                                helperText={touched.email && errors.email}
                                error={Boolean(errors.email && touched.email)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="mobile_no" className='form-label'>Mobile No.</label>
                              {/* <PhoneInput
                        country={"in"}
                        value={values.mobile_no}
                        name="mobile_no"
                        onChange={handleChange}
                        onBlur={(e) => handleBlur(e)}
                        defaultMask=".... ... ..."
                        masks={{ in: ".........." }}
                        onlyCountries={["in"]}
                        inputProps={{
                          name: "mobile_no",
                          required: true,
                        }}
                        disableSearchIcon={true}
                        disableDropdown={true}
                        containerClass="h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-transparent border-transparent focus:bg-white"

                        // containerClass="h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-transparent border-transparent focus:bg-white"
                        inputClass="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-transparent border-transparent focus:bg-white"
                        inputStyle={{
                          background: "transparent",
                          // border: "1px solid grey",
                          height: "2.70em",
                          width: "100%",
                          outline: "none",

                        }}
                        buttonStyle={{
                          // height: "3em",
                          // background: "transparent",
                          // outline: "none",
                          // border: "none",
                        }}
                        style={{ marginBottom: "22px" }}
                      /> */}
                              <TextField
                                fullWidth
                                disabled
                                size="small"
                                type="number"
                                name="mobile_no"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.mobile_no}
                                onChange={handleChange}
                                helperText={touched.mobile_no && errors.mobile_no}
                                error={Boolean(errors.mobile_no && touched.mobile_no)}
                                sx={{ mb: 3 }}
                                inputProps={
                                  { maxLength: 10 }
                                }
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="firm_name" className='form-label'>Firm Name</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="firm_name"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.firm_name}
                                onChange={handleChange}
                                helperText={touched.firm_name && errors.firm_name}
                                error={Boolean(errors.firm_name && touched.firm_name)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="plan_id" className='form-label'>Plan</label>
                              <FormControl fullWidth
                                sx={{ mb: 3 }}
                                size="small"
                              >
                                <Select
                                  labelId="status-label"
                                  id="plan_id"
                                  name="plan_id"
                                  value={values.plan_id}
                                  onChange={handleChange}
                                  variant="outlined"
                                  onBlur={handleBlur}
                                  error={Boolean(errors.plan_id && touched.plan_id)}
                                >
                                  <MenuItem value="" key="sg">Select Plan</MenuItem>
                                  {plans && plans.map((row) =>
                                    <MenuItem value={row.id} key={row.id}>{row.plan_name}</MenuItem>
                                  )}
                                </Select>
                              </FormControl>
                            </Grid>


                            <Grid item sm={6} xs={12}>
                              <label htmlFor="address" className='form-label'>Address</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="address"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.address}
                                onChange={handleChange}
                                helperText={touched.address && errors.address}
                                error={Boolean(errors.address && touched.address)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>

                            <Grid item sm={6} xs={12}>
                              <label htmlFor="state_id" className='form-label'>State</label>
                              <FormControl fullWidth
                                sx={{ mb: 3 }}
                                size="small"
                              >
                                <Select
                                  labelId="state-label"
                                  id="brand_id"
                                  name="state_id"
                                  // label="Select States"
                                  value={values.state_id}
                                  onChange={handleChange}
                                  variant="outlined"
                                  onBlur={handleBlur}
                                  // defaultValue={values.state_id.toString()}
                                  error={Boolean(errors.state_id && touched.state_id)}
                                >
                                  <MenuItem value="" key="sg">Select State</MenuItem>
                                  {states && states.map((row) =>
                                    <MenuItem value={row.id} key={row.id}>{row.name} ({row.state_code})</MenuItem>
                                  )}
                                </Select>
                              </FormControl>
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="pin_code" className='form-label'>Pin Code</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="number"
                                name="pin_code"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.pin_code}
                                onChange={handleChange}
                                helperText={touched.pin_code && errors.pin_code}
                                error={Boolean(errors.pin_code && touched.pin_code)}
                                sx={{ mb: 3 }}
                                inputProps={{ min: 6 }}
                              />
                            </Grid>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="gst_no" className='form-label'>GST No</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="gst_no"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.gst_no}
                                onChange={handleChange}
                                helperText={touched.gst_no && errors.gst_no}
                                error={Boolean(errors.gst_no && touched.gst_no)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                          </Grid>
                          <Grid container style={{ padding: '0px 20px' }} spacing={2}>
                            <Grid item sm={12} xs={12} lg={12}>
                              <h3 style={{ marginLeft: 0, fontSize: '17px' }}>Categories</h3>
                            </Grid>
                            <FormControl sx={{ mb: 3 }} component="fieldset" variant="standard" style={{ padding: "0px 0px 0px 16px" }}>
                              <FormGroup row={true}>
                                {categories ? categories
                                  .map((row, index) => (
                                    <Grid item sm={6} xs={12} lg={4} key={index}>
                                      <FormControlLabel
                                        control={
                                          <Checkbox
                                            checked={userCategories.includes(row.id)}
                                            name={`user_categories[${row.id}]`}
                                            value={row.id}
                                            onChange={e => {
                                              if (e.target.checked) {
                                                userCategories.push(row.id);
                                                console.log(userCategories);
                                                setFormData({ ...formData, ['user_categories']: userCategories });
                                                console.log(userCategories[0]);
                                                if (userCategories.length == 1) {
                                                  if (userCategories[0] == 5) {
                                                    setDLOpen(false);
                                                    setFssaiOpen(false);
                                                  } else if (userCategories[0] == 4) {
                                                    setDLOpen(false);
                                                    setFssaiOpen(true);
                                                  } else {
                                                    setDLOpen(true);
                                                    setFssaiOpen(true);
                                                  }
                                                } else if (userCategories.length) {
                                                  setDLOpen(true);
                                                  setFssaiOpen(true);
                                                } else {
                                                  setDLOpen(false);
                                                  setFssaiOpen(false);
                                                }
                                              } else {
                                                const updateUserCat = formData.user_categories.filter((rowLal) => rowLal !== (row.id));
                                                setUserCategories(updateUserCat);
                                                setFormData({ ...formData, ['user_categories']: updateUserCat });
                                                console.log(updateUserCat[0]);
                                                if (updateUserCat.length == 1) {
                                                  if (updateUserCat[0] == 5) {
                                                    setDLOpen(false);
                                                    setFssaiOpen(false);
                                                  } else if (updateUserCat[0] == 4) {
                                                    setDLOpen(false);
                                                    setFssaiOpen(true);
                                                  } else {
                                                    setDLOpen(true);
                                                    setFssaiOpen(true);
                                                  }
                                                } else if (updateUserCat.length) {
                                                  setDLOpen(true);
                                                  setFssaiOpen(true);
                                                } else {
                                                  setDLOpen(false);
                                                  setFssaiOpen(false);
                                                }
                                              }
                                            }}
                                          />
                                        }
                                        label={row.name}
                                      />

                                    </Grid>
                                  )) : <></>}
                              </FormGroup>

                              {/* {(!formData.user_categories.length) && (errors.user_categories)
                                && (touched.user_categories)
                                && <div style={{ color: '#ff3d57' }}>{`${errors.user_categories}`}</div>} */}
                            </FormControl>
                          </Grid>
                          {DLOpen && (
                            <Grid container style={{ padding: '0px 20px' }} spacing={2}>
                              <Grid item sm={6} xs={12}>
                                <label htmlFor="dl_no_1" className='form-label'>DL NO 1</label>
                                <TextField
                                  fullWidth
                                  size="small"
                                  type="text"
                                  name="dl_no_1"
                                  // label="Name"
                                  variant="outlined"
                                  onBlur={handleBlur}
                                  value={(values.dl_no_1) ? values.dl_no_1 : ''}
                                  onChange={handleChange}
                                  helperText={touched.dl_no_1 && errors.dl_no_1}
                                  error={Boolean(errors.dl_no_1 && touched.dl_no_1)}
                                  sx={{ mb: 3 }}
                                />
                              </Grid>
                              <Grid item sm={6} xs={12}>
                                <label htmlFor="dl_no_2" className='form-label'>DL NO 2</label>
                                <TextField
                                  fullWidth
                                  size="small"
                                  type="text"
                                  name="dl_no_2"
                                  // label="Name"
                                  variant="outlined"
                                  onBlur={handleBlur}
                                  value={(values.dl_no_2) ? values.dl_no_2 : ''}
                                  onChange={handleChange}
                                  helperText={touched.dl_no_2 && errors.dl_no_2}
                                  error={Boolean(errors.dl_no_2 && touched.dl_no_2)}
                                  sx={{ mb: 3 }}
                                />
                              </Grid>
                            </Grid>
                          )}
                          {fssaiOpen && (<Grid container style={{ padding: '0px 20px' }} spacing={2}>
                            <Grid item sm={6} xs={12}>
                              <label htmlFor="fssai_no" className='form-label'>FSSAI No</label>
                              <TextField
                                fullWidth
                                size="small"
                                type="text"
                                name="fssai_no"
                                // label="Name"
                                variant="outlined"
                                onBlur={handleBlur}
                                value={values.fssai_no}
                                onChange={handleChange}
                                helperText={touched.fssai_no && errors.fssai_no}
                                error={Boolean(errors.fssai_no && touched.fssai_no)}
                                sx={{ mb: 3 }}
                              />
                            </Grid>
                          </Grid>
                          )}
                          <Grid container style={{ padding: '0px 20px' }} spacing={2}>
                            <Grid item sm={6} xs={12} lg={12}>
                              <label htmlFor="last_name" className='form-label'>Register As</label>
                              <RadioGroup
                                row
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue="M"
                                name="register_as"
                                sx={{ mb: 2 }}
                                value={values.register_as}
                                onChange={handleChange}
                              >
                                <FormControlLabel value="M" control={<Radio />} label="Manufacturer" />
                                <FormControlLabel value="D" control={<Radio />} label="Distributor" />
                                <FormControlLabel value="W" control={<Radio />} label="Wholesaler" />
                                <FormControlLabel value="R" control={<Radio />} label="Retailer" />
                                <FormControlLabel value="H" control={<Radio />} label="Hospital" />
                              </RadioGroup>
                            </Grid>
                            {(values.register_as == 'M' || values.register_as == 'D' || values.register_as == 'W') ? (
                              <>
                                <Grid item sm={12} xs={12} lg={12} style={{ paddingTop: "10px" }}>
                                  <h3 style={{ color: theme.palette.primary.main, marginLeft: 0, fontSize: '17px' }}>Bank Details</h3>
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                  <label htmlFor="account_name" className='form-label'>Account Name</label>
                                  <TextField
                                    fullWidth
                                    size="small"
                                    type="text"
                                    name="account_name"
                                    // label="Name"
                                    variant="outlined"
                                    onBlur={handleBlur}
                                    value={values.account_name}
                                    onChange={handleChange}
                                    helperText={touched.account_name && errors.account_name}
                                    error={Boolean(errors.account_name && touched.account_name)}
                                    sx={{ mb: 3 }}
                                  />
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                  <label htmlFor="account_no" className='form-label'>Account No</label>
                                  <TextField
                                    fullWidth
                                    size="small"
                                    type="number"
                                    name="account_no"
                                    // label="Name"
                                    variant="outlined"
                                    onBlur={handleBlur}
                                    value={values.account_no}
                                    onChange={handleChange}
                                    helperText={touched.account_no && errors.account_no}
                                    error={Boolean(errors.account_no && touched.account_no)}
                                    sx={{ mb: 3 }}
                                  />
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                  <label htmlFor="ifsc_code" className='form-label'>IFSC Code</label>
                                  <TextField
                                    fullWidth
                                    size="small"
                                    type="text"
                                    name="ifsc_code"
                                    // label="Name"
                                    variant="outlined"
                                    onBlur={handleBlur}
                                    value={values.ifsc_code}
                                    onChange={handleChange}
                                    helperText={touched.ifsc_code && errors.ifsc_code}
                                    error={Boolean(errors.ifsc_code && touched.ifsc_code)}
                                    sx={{ mb: 3 }}
                                  />
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                  <label htmlFor="branch" className='form-label'>Branch</label>
                                  <TextField
                                    fullWidth
                                    size="small"
                                    type="text"
                                    name="branch"
                                    // label="Name"
                                    variant="outlined"
                                    onBlur={handleBlur}
                                    value={values.branch}
                                    onChange={handleChange}
                                    helperText={touched.branch && errors.branch}
                                    error={Boolean(errors.branch && touched.branch)}
                                    sx={{ mb: 3 }}
                                  />
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                  <label htmlFor="first_name" className='form-label'>Upload Signature</label>
                                  {values.signature && (
                                    <>
                                      <br />
                                      <img src={values.signature} style={{ width: '100px' }} alt="Signature" />
                                      <br />
                                    </>
                                  )}
                                  <FormControl fullWidth={true} sx={{ mb: 3 }}>
                                    <input
                                      id="upload-photo"
                                      name="update_signature"
                                      type="file"
                                      onChange={changeHandler}
                                    />
                                  </FormControl>
                                  {(errors.update_signature)
                                    && (touched.update_signature)
                                    && <div style={{ color: '#ff3d57' }}>{`${errors.update_signature}`}</div>}
                                </Grid>
                              </>
                            )
                              : <></>}
                          </Grid>
                          <Grid container style={{ padding: '30px 20px 0px' }} spacing={4}>
                            <Grid item sm={6} xs={12} lg={4}>
                              <LoadingButton
                                fullWidth
                                type="submit"
                                color="primary"
                                loading={loading}
                                variant="contained"
                                sx={{ mb: 2, mt: 3 }}
                              >
                                Update
                              </LoadingButton>
                            </Grid>
                          </Grid>
                          <br />
                        </form>
                      )}
                    </Formik>

                    {/* {user.userCategories ? user.userCategories
                      .map((row, index) => (
                        <Grid container spacing={2} key={'1_gc_' + (1 + index)}>
                          <Grid item sm={6} xs={12} lg={3} key={'1_g_' + (1 + index)}>
                            <Item key={'1_gi_' + (1 + index)}>Category Name</Item>
                            <ItemContent key={'1_gic_' + (1 + index)}>
                              {user.userCategories[index].medicine_category}
                            </ItemContent>
                          </Grid>
                          <Grid item sm={6} xs={12} lg={3} key={'2_g_' + (1 + index)}>
                            <Item key={'2_gi_' + (1 + index)}>Commission %</Item>
                            <ItemContent key={'2_gic_' + (1 + index)}>
                              {user.userCategories[index].commission}
                            </ItemContent>
                          </Grid>
                          <Grid item sm={6} xs={12} lg={3} key={'3_g_' + (1 + index)}>
                            <Item key={'3_gi_' + (1 + index)}>Default Commission %</Item>
                            <ItemContent key={'3_gic_' + (1 + index)}>
                              {user.userCategories[index].default_commission} %
                            </ItemContent>
                          </Grid>
                        </Grid>
                      )) : <></>} */}

                  </>
                )}
              </Box>
            </CardContent>
          </ContentBox>
        </Card>

      </Container>
    </>

  );
};

export default UpdateProfile;
