import React from 'react';
import SliderCard from './SliderCard';
import { Link } from 'react-router-dom';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

function BannerSlider() {
  const options = {
    nav: true,
    dots: false,
    // autoplay: true,
    loop: true,
    margin: 0,
    responsiveClass: true,
    navText: ['<i class="ion-ios-arrow-left"></i>', '<i class="ion-ios-arrow-right"></i>'],
    smartSpeed: 1000,
    responsive: {
      0: {
        items: 1,
      },
      400: {
        items: 2,
      },
      600: {
        items: 2,
      },
      700: {
        items: 3,
      },
      1000: {
        items: 3,

      }
    },
  };
  return (
    <>
      <div className="section">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="slide marg-btm30">
                <OwlCarousel className='product_slider carousel_slider owl-carousel owl-theme nav_style1'
                  {...options}>
                  <div className="item">
                    <div className="product">
                      <div className="product_img">
                        <Link to={`/categories/3`}>
                          <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/surgical.png`} alt={'Surgical & Medical Equipment'} />
                        </Link>
                      </div>
                      <div className="product_info">
                        <h6 className="product_title">
                          <Link to={`/categories/3`}>{'Surgical & Medical Equipment'}</Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <div className="product">
                      <div className="product_img">
                        <Link to={`/categories/1`}>
                          <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/allopathy.png`} alt={'Allopathy + Generic'} />
                        </Link>
                      </div>
                      <div className="product_info">
                        <h6 className="product_title">
                          <Link to={`/categories/1`}>{'Allopathy + Generic'}</Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <div className="product">
                      <div className="product_img">
                        <Link to={`/categories/5`}>
                          <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/ayurvedic.png`} alt={'Ayurvedic'} />
                        </Link>
                      </div>
                      <div className="product_info">
                        <h6 className="product_title">
                          <Link to={`/categories/5`}>{'Ayurvedic'}</Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <div className="product">
                      <div className="product_img">
                        <Link to={`/categories/4`}>
                          <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/otc.png`} alt={'OTC'} />
                        </Link>
                      </div>
                      <div className="product_info">
                        <h6 className="product_title">
                          <Link to={`/categories/4`}>{'OTC'}</Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="item">
                    <div className="product">
                      <div className="product_img">
                        <Link to={`/categories/6`}>
                          <img src={`${process.env.PUBLIC_URL}/assets/images/frontend/veterinary.png`} alt={'Veterinary'} />
                        </Link>
                      </div>
                      <div className="product_info">
                        <h6 className="product_title">
                          <Link to={`/categories/6`}>{'Veterinary'}</Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                </OwlCarousel>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default BannerSlider;
