import React, { useEffect, useState, SyntheticEvent } from 'react';
import { Link, useParams, NavLink, useNavigate } from 'react-router-dom';
import useAuth from 'app/hooks/useAuth';
import { useCart } from 'app/contexts/CartContext';
import Header from './../Header/Header';
import Footer from './../Footer/Footer';
import RelatedProducts from './RelatedProducts';

import { Formik } from 'formik';
import { LoadingButton } from '@mui/lab';

import * as Yup from 'yup';
import {
  Grid, Tab, Box, Card, CardContent, Badge,
  TextField,
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper,
  Backdrop, CircularProgress
} from '@mui/material';

import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';

import axios from 'axios';
import swal from 'sweetalert';
import Moment from 'moment';

// form field validation schema
const validationSchema = Yup.object().shape({
  quantity: Yup.string()
    .required('Quantity is required!'),
});

function ProductDetail() {
  let { isAuthenticated,
    // user
  } = useAuth();
  const params = useParams();
  const navigate = useNavigate();

  const [product, setProduct] = useState(false);
  const [loading, setLoading] = useState(false);
  const [slug, setSlug] = useState(params.slug);
  const [row_id, setRowId] = useState(params.row_id);
  const [initialValues, setInitialValues] = useState({
    quantity: '',
  });
  const { increaseCartQuantity } = useCart();
  useEffect(() => {
    if (row_id !== params.row_id) {
      setRowId(params.row_id);
    } else {
      // window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
      fetchData(slug);
    }
  }, [slug, row_id, navigate]);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/frontend/get-product-detail', { 'slug': slug, 'row_id': row_id })
          .then(response => {
            if (response.data.response_code === 200) {
              setInitialValues({
                quantity: response.data.data.min_qty,
              });
              setProduct(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.log(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  const [value, setValue] = React.useState('1');

  const handleChange = (event: React.SyntheticEvent, newValue: string) => {
    setValue(newValue);
  };

  const handleFormSubmit = async (values, errors) => {
    if (!isAuthenticated) {
      navigate('/login');
      return false;
    }

    if (values.quantity > product.available_qty) {
      return errors.setErrors({ 'quantity': 'Quantity should be <= to available quantity!' });
    }
    if (values.quantity < product.min_qty) {
      return errors.setErrors({ 'quantity': 'Quantity should be >= to minimum quantity!' });
    }

    setLoading(true);
    increaseCartQuantity(row_id, values.quantity);
    setLoading(false);
  };
  return (
    <>
      <Header />
      <div className="breadcrumb_section  page-title-mini" style={{ padding: '30px 0px' }}>
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <ol className="breadcrumb ">
                <li className="breadcrumb-item"><Link to="/">Home</Link></li>
                <li className="breadcrumb-item"><Link to="/categories?q=">Category</Link></li>
                <li className="breadcrumb-item active">Product 1</li>
              </ol>
            </div>
          </div>
        </div>
      </div>
      <Backdrop
        sx={{ color: '#015998', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={loading}
        onClick={setLoading}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
      <div className="section marg-btm30">
        <div className="container" style={{ position: "relative", minHeight: "400px" }}>
          {product && (
            <Grid container item rowSpacing={3} columnSpacing={5}>
              <Grid item lg={7} sm={12} xs={12}>
                <div className="align-items-center prod-detail prod-detail-thumb marg-btm30">
                  <div className="col-sm-12">
                    <img src={`${product.product_image}`} alt={product.product_name} />
                  </div>
                </div>
              </Grid>
              <Grid item lg={5} sm={12} xs={12}>
                <div className="row align-items-center prod-detail ">
                  <div className="col-sm-12">
                    <h1 className="btn-shine">{product.product_name}</h1>
                    <p><b>Manufacturer:</b> {product.brand_name} </p>
                    <p><b>Category:</b> {product.category_name}</p>
                    <p><b>Sold By Seller: </b>  {product.seller_name}</p>
                    <Card variant="outlined" style={{ backgroundColor: "#F4F3F3" }}>
                      <CardContent>
                        <div>
                          <Badge badgeContent={''} color="primary"></Badge>
                          <span className='price'> {process.env.REACT_APP_CURRENCY} {product.min_qty_net_rate}</span>
                        </div>
                        <div>
                          <span style={{ padding: '0px 20px' }}>(Inclusive of all taxes)</span>
                        </div>
                        {/* <div>
                          <span style={{ padding: '0px 20px' }}>Earliest delivery by Sunday, 8 January</span>
                        </div> */}
                        <div style={{ padding: '20px 20px 0px 20px' }}>
                          <Formik
                            onSubmit={(values, errors) => {
                              handleFormSubmit(values, errors);
                            }
                            }
                            initialValues={initialValues}
                            validationSchema={validationSchema}
                          >
                            {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
                              <form onSubmit={handleSubmit}>
                                <div className="form-group">
                                  <TextField
                                    className="login_mobile_no122 form-control1"
                                    size="small"
                                    type="number"
                                    placeholder="Enter Quantity"
                                    name="quantity"
                                    onBlur={handleBlur}
                                    value={values.quantity}
                                    onChange={handleChange}
                                    helperText={touched.quantity && errors.quantity}
                                    error={Boolean(errors.quantity && touched.quantity)}
                                    inputProps={{ maxLength: 10 }}
                                    sx={{ width: '130px', background: "#FFF" }}
                                  />
                                  <span className='avail-qty'>Available Quantity: {product.available_qty}</span>
                                </div>
                                <div className="form-group">
                                  <LoadingButton
                                    className="btn btn-fill-out btn-block"
                                    type="submit"
                                    color="primary"
                                    loading={loading}
                                    variant="contained"
                                    sx={{ mt: 2 }}
                                  >
                                    Add to cart
                                  </LoadingButton>
                                </div>
                              </form>
                            )}
                          </Formik>
                        </div>
                      </CardContent>
                    </Card>
                    <div className='row sel-detail'>
                      <div className='col-sm-12 col-md-6'>Sold By Seller: {product.seller_name}</div>
                      <div className='col-sm-12 col-md-6' style={{ textAlign: "right" }}></div>
                      <div className='col-sm-12 col-md-6'>Expiry Date: {(product.expire_date) ? Moment(product.expire_date).format('DD MMM YYYY') : 'NA'}</div>
                      <div className='col-sm-12 col-md-6' style={{ textAlign: "right" }}>
                        MRP: {process.env.REACT_APP_CURRENCY} {product.mrp}
                      </div>
                    </div>

                    <TableContainer component={Paper}>
                      <Table aria-label="simple table">
                        <TableHead>
                          <TableRow sx={{ background: "#f4f3f3" }}>
                            <TableCell align="center">Quantity</TableCell>
                            <TableCell align="center">Discount</TableCell>
                            <TableCell align="center" sx={{ width: "100px" }}>Bonus Deal</TableCell>
                            <TableCell align="center">Trade Rate</TableCell>
                            <TableCell align="center">Net Rate</TableCell>
                            <TableCell align="center">Margin%</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          <TableRow
                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                          >
                            <TableCell align="center">{product.min_qty}</TableCell>
                            <TableCell align="center">{product.min_qty_discount}%</TableCell>
                            <TableCell align="center">{product.min_qty_bonus_deal}</TableCell>
                            <TableCell align="center">{product.min_qty_trade_rate}</TableCell>
                            <TableCell align="center">{product.min_qty_net_rate}</TableCell>
                            <TableCell align="center">{product.min_qty_margin_percentage}%</TableCell>
                          </TableRow>
                        </TableBody>
                      </Table>
                    </TableContainer>
                  </div>
                </div>
              </Grid>
              <Grid item lg={12} sm={12} xs={12}>
                <br />
                <TabContext value={value}>
                  <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <TabList onChange={handleChange} aria-label="Product details">
                      <Tab label="Description" value="1" />
                      <Tab label="Additional information" value="2" />
                      <Tab label="Reviews" value="3" />
                    </TabList>
                  </Box>
                  <TabPanel value="1">{product.description}</TabPanel>
                  <TabPanel value="2"></TabPanel>
                  <TabPanel value="3"></TabPanel>
                </TabContext>
              </Grid>
            </Grid>
          )}

          <RelatedProducts />
        </div>
      </div >
      <Footer />
    </>
  );
}
export default ProductDetail;
