import React, { createContext, useEffect, useReducer } from 'react'
import jwtDecode from 'jwt-decode'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { MatxLoading } from 'app/components'
import swal from 'sweetalert';

const initialState = {
    isAuthenticated: false,
    isInitialised: false,
    user: null,
}

const isValidToken = (accessToken) => {
    if (!accessToken) {
        return false
    } else {
        return true;
    }

    // const decodedToken = jwtDecode(accessToken)
    // const currentTime = Date.now() / 1000
    // return decodedToken.exp > currentTime
}

const setSession = (accessToken) => {
    if (accessToken) {
        localStorage.setItem('accessToken', accessToken)
        // axios.defaults.headers.common.Authorization = `Bearer ${accessToken}`
    } else {
        localStorage.removeItem('accessToken')
        // delete axios.defaults.headers.common.Authorization
    }
}

const reducer = (state, action) => {
    switch (action.type) {
        case 'INIT': {
            const { isAuthenticated, user } = action.payload
            return {
                ...state,
                isAuthenticated,
                isInitialised: true,
                user,
            }
        }
        case 'LOGIN': {
            const { user } = action.payload
            return {
                ...state,
                isAuthenticated: true,
                user,
            }
        }
        case 'LOGOUT': {
            return {
                ...state,
                isAuthenticated: false,
                user: null,
            }
        }
        case 'REGISTER': {
            const { user } = action.payload

            return {
                ...state,
                isAuthenticated: true,
                user,
            }
        }
        default: {
            return { ...state }
        }
    }
}

const AuthContext = createContext({
    ...initialState,
    method: 'JWT',
    login: () => Promise.resolve(),
    logout: () => { },
    register: () => Promise.resolve(),
})

export const AuthProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialState)
    const navigate = useNavigate();
    const login = async (mobile_no, login_otp) => {
        axios.get('/sanctum/csrf-cookie').then(async response => {
            await axios.post('/api/client/verify-otp', {
                mobile_no: mobile_no,
                login_otp: login_otp,
            }).then(response => {
                if (response.data.response_code === 200) {
                    var responseData = response.data.data;
                    localStorage.setItem('accessToken', responseData.token);
                    localStorage.setItem('auth_name', responseData.user_name);
                    localStorage.setItem('register_as', responseData.register_as);
                    swal({
                        title: "Good job!",
                        text: response.data.message,
                        icon: "success",
                    });
                    const user = responseData
                    const token = responseData.token
                    setSession(token)
                    dispatch({
                        type: 'LOGIN',
                        payload: {
                            user,
                        },
                    })
                    if (responseData.register_as === "M") {
                        navigate('/dashboard');
                    } else {
                        navigate('/');
                    }
                } else if (response.data.response_code === 401 || response.data.response_code === '401') {
                    swal({
                        title: "Warning!",
                        text: response.data.message,
                        icon: "warning",
                    });
                } else {
                }
            }).catch(function (error) {
                console.error(error);
            });
        });

    }

    const register = async (email, username, password) => {
        const response = await axios.post('/api/client/register', {
            email,
            username,
            password,
        })

        const { accessToken, user } = response.data

        setSession(accessToken)

        dispatch({
            type: 'REGISTER',
            payload: {
                user,
            },
        })
    }

    const logout = () => {
        axios.get('/sanctum/csrf-cookie').then(async response => {
            await axios.post('/api/client/logout').then(response => {
                if (response.data.response_code === 200) {
                    localStorage.removeItem('accessToken');
                    localStorage.removeItem('auth_name');
                    localStorage.removeItem('register_as');
                    localStorage.removeItem('login-mobile-no');
                    swal({
                        title: "Good job!",
                        text: response.data.message,
                        icon: "success",
                    });
                    setSession(null)
                    dispatch({ type: 'LOGOUT' })
                    navigate('/');
                } else if (response.data.response_code === 401) {
                    swal({
                        title: "Warning!",
                        text: response.data.message,
                        icon: "warning",
                    });
                } else {
                    swal({
                        title: "Warning!",
                        text: response.data.message,
                        icon: "warning",
                    });
                }
            }).catch(function (error) {
                console.error(error);
            });
        });
    }

    useEffect(() => {
        ; (async () => {
            try {
                const accessToken = window.localStorage.getItem('accessToken')
                if (accessToken && isValidToken(accessToken)) {
                    setSession(accessToken)
                    axios.post(`/api/client/profile`).then(response => {
                        if (response.data.response_code === 200) {
                            const user = response.data.data;
                            dispatch({
                                type: 'INIT',
                                payload: {
                                    isAuthenticated: true,
                                    user,
                                },
                            })
                        } else {
                            localStorage.removeItem('accessToken');
                            localStorage.removeItem('auth_name');
                            localStorage.removeItem('register_as');
                            localStorage.removeItem('login-mobile-no');
                            dispatch({
                                type: 'INIT',
                                payload: {
                                    isAuthenticated: false,
                                    user: null,
                                },
                            })
                        }
                    }).catch(function (error) {
                        localStorage.removeItem('accessToken');
                        localStorage.removeItem('auth_name');
                        localStorage.removeItem('register_as');
                        localStorage.removeItem('login-mobile-no');
                        dispatch({
                            type: 'INIT',
                            payload: {
                                isAuthenticated: false,
                                user: null,
                            },
                        })
                    });
                } else {
                    localStorage.removeItem('accessToken');
                    localStorage.removeItem('auth_name');
                    localStorage.removeItem('register_as');
                    localStorage.removeItem('login-mobile-no');
                    dispatch({
                        type: 'INIT',
                        payload: {
                            isAuthenticated: false,
                            user: null,
                        },
                    })
                }
            } catch (err) {
                console.error(err)
                localStorage.removeItem('accessToken');
                localStorage.removeItem('auth_name');
                localStorage.removeItem('register_as');
                localStorage.removeItem('login-mobile-no');
                dispatch({
                    type: 'INIT',
                    payload: {
                        isAuthenticated: false,
                        user: null,
                    },
                })
            }
        })()
    }, [])

    if (!state.isInitialised) {
        return <MatxLoading />
    }

    return (
        <AuthContext.Provider
            value={{
                ...state,
                method: 'JWT',
                login,
                logout,
                register,
            }}
        >
            {children}
        </AuthContext.Provider>
    )
}

export default AuthContext
